function GamePlay(){

	var self = this

	var interval
	var items

	var avail_time

	var def_avail_time = 50
	var bonus_time = 10
	var bonus_reduce_by = .4

	var tickClb
	var overClb

	var elements
	var used
	var currentIndex

	this.over = false


	this.reset = function(){
		console.log('Game::reset')
		interval = null
		avail_time = def_avail_time
		items = 0
		this.over = false

		used = elements.slice(0)
		this.selectItem()
	}


	function createInterval(){
		interval = setInterval(function(){
			avail_time -= 1
			avail_time = parseInt(avail_time)
			var perc =  parseInt(avail_time*100/def_avail_time)
			tickClb(avail_time, perc)
			
			if(avail_time <= 0){
				self.abort()
				self.over = true
				overClb()
			}
		}, 1000)
	}

	this.getCurrentVal = function(){
		return {v:avail_time, p:avail_time*100/def_avail_time}
	}

	this.config = function(el){
		console.log('Game::config', el)
		elements = []
		el.each(function(i, e){
			elements.push(e)
		})
		used = elements.slice(0)
		this.selectItem()
	}


	this.start = function(){
		console.log('Game::start')
		createInterval()
	}


	this.getCurrent = function(){
		return currentIndex
	}


	this.getCurrentName = function(){
		var id = $(elements[currentIndex]).attr('id')
		console.log(id, currentIndex, used.length)
		var n = id.replace(/_\d_/g, '')
		return n
	}

	this.getLength = function(){
		return used.length
	}


	this.pause = function(){
		console.log('Game::pause')
		clearInterval(interval)
	}

	this.continue = function(){
		console.log('Game::continue')
		createInterval()
	}



	this.addItem = function(o){
		console.log('Game::addItem')
		items += 1
		avail_time += (bonus_time-bonus_reduce_by*items)
		avail_time = Math.min(avail_time, def_avail_time)
		this.pause()
		
		if(used.length>0) this.selectItem();
	}

	this.selectItem = function(){
		var index = parseInt(Math.random()*used.length)
		var obj = used[index]
		used.splice(index, 1)
		currentIndex = elements.indexOf(obj) //8
	}


	this.abort = function(){
		console.log('Game::abort')
		clearInterval(interval)
	}



	this.onTick = function(clb){
		tickClb = clb
	}

	this.onOver = function(clb){
		overClb = clb
	}

	this.getMissed = function(){
		return elements.length - items
	}
	
	this.getItems = function(){
		return items
	}

	this.getTotal = function(){
		return elements.length
	}

	return this

}