function CatchState(){

	APP.catch.hide()

	var timer

	return {
		enter: function(option){ console.log('CatchState :: enter');
			

			var dato = APP.getDato()
			console.log(dato)

			APP.catch.setAct(APP.labels.nexttarget_label)
			APP.catch.setTitle(dato.you_found)
			APP.catch.setText(dato.description)

			var index = APP.game.getCurrent()
			APP.catch.show(index)

			APP.game.addItem()

			var datonext = APP.getDato()
			console.log(datonext)
			
			APP.panel.setTarget( APP.game.getCurrent() )
			APP.panel.setPartial('find')
			APP.panel.setButtonLabel( APP.labels.next_target_panel_button )
			APP.panel.setAction( datonext.introduction_text )
			APP.panel.updateNumber(APP.game.getItems(), APP.game.getTotal())
			APP.panel.show(3000)

			APP.bar.setNum( APP.game.getItems() )
			APP.bar.setBarIcon(1)
			var vals = APP.game.getCurrentVal()
	    	APP.bar.setTime(vals.v, vals.p)


			// if(APP.game.getLength() == 0){
			// 	clearTimeout(timer)
	  //   		APP.game.abort()
		 //    	APP.bar.setTime(0,0)
			// 	APP.panel.setAction( APP.labels.nexttarget_panel_label )
		 //    	APP.panel.setTitle('You win!')
		 //    	APP.panel.setItemsNum( APP.game.getItems() )
	  //   	}

			timer = setTimeout(function(){
				APP.back.pick( APP.game.getCurrent() )
			}, 1000)
		},
		leave: function(option){ console.log('CatchState :: leave')
			APP.catch.hide()
			APP.panel.signalTarget()

			APP.panel.hide(1000, 500)
			APP.game.continue()

			APP.bar.setTarget( APP.game.getCurrent() )
			APP.bar.setBarIcon(0)
			APP.bar.signalTarget()

			clearTimeout(timer)
		}
	}

}
