function LoadingState(){

	return {
		enter: function(option){ console.log('LoadingState :: enter');

			$(document).ready(function(){

				$.get('assets/labels.csv', function(data){

					var csv = d3.csvParse(data)

					APP.labels = {}

					csv.forEach(function(d, i){
						APP.labels[d.Label_id] = d.Text
					})

					console.log(APP.labels)

					csv.forEach(function(d, i){
						$('['+d.Label_id+']').html(d.Text)
					})

					$.get('assets/data.csv', function(data){
						APP.db = d3.csvParse(data)
						console.log(APP.db)

						console.log(APP.back.elements)

						APP.back.init()
						APP.game.config( APP.back.elements )

						console.log('APP.stateman.current.name', APP.stateman.current.name)
						APP.stateman.go('home')
					})
				})

			})

				
		},
		leave: function(option){ console.log('LoadingState :: leave')

			$('#loading').hide()
		}
	}

}
