;(function(window, $, undefined){

	APP.catch = {}

	$(document).ready(function(){

		console.log('ready')

		var cont = $('#catch')

		var act = cont.find('span')
		var title = cont.find('h1')
		var desc = cont.find('p')

		var imgFound = cont.find('#imgFound').css({opacity:0})
		var disc = cont.find('#ball').css({scale:0, opacity:0})

		// var ball = svgFound.find('#ball').css({opacity:0, scale: 0, transformOrigin: '50% 50%'})

		// var width_view = $(window).width()
  //       var width_content = imgFound.width()
  //       imgFound.css({x:(width_content-width_view)/2*-1})

		var p = cont.find('.middle .wrapper > *').css({opacity:0})

		function show(item){
				
			cont.show()

			var scale = ($('html').hasClass('portrait')) ? 2 : 3.5

			disc.css({scale:0, opacity:1})


			var num = (item<9) ? '0'+(item+1) : item+1
			imgFound.find('img').attr('src', 'assets/you_found_jpg/you_found_' + num + '.jpg')
			imgFound.css({opacity:0})
			

			disc.transition({scale:scale, transformOrigin: '50% 50%', complete:function(){

				showTextOnly()

				imgFound.css({opacity:1})

				disc.transition({opacity:0, delay:200}, 1000, 'easeInOutQuart')

			}}, 750, 'easeInOutQuint')

			
			
		}

		function showTextOnly(){
			p.each(function(i, e){
				$(this).transition({opacity:1, delay:i*200 + 500}, 1000)
			})
			cont.removeClass('bypass')
		}




		function hide(){
			p.each(function(i, e){
				$(this).transition({opacity:0}, 1000)
			})
			cont.addClass('bypass')

			imgFound.transition({opacity:0}, 500)

			setTimeout(function(){
				cont.hide()
				imgFound.attr('src', null)
			}, 1000)
			
		}

		function setTitle(t){
			title.text(t)
		}

		function setText(t){
			desc.text(t)
		}

		function setAct(t){
			act.text(t)
			
		}

		APP.catch.showTextOnly = showTextOnly
		APP.catch.setTitle = setTitle
		APP.catch.setAct = setAct
		APP.catch.setText = setText
		APP.catch.hide = hide
		APP.catch.show = show
	})



})(window, jQuery)