function OverState(){

	return {
		enter: function(option){ console.log('OverState :: enter');

			var res = APP.game.getItems()

			$('#fume').transition({opacity:1}, 500)

			APP.panel.setAction( APP.labels.gameover_panel_label )

			var str = APP.labels.gameover_panel_title.replace('XX', res)
			APP.panel.setTitle(str)
			APP.panel.setButton('#home')
			APP.panel.setButtonLabel( APP.labels.gameover_panel_button )
			APP.panel.setPartial('over')
			APP.panel.setTarget(-1)
			APP.panel.setGameOverTitle(str)
			APP.panel.show()


			var dato = APP.getDato()
			console.log(dato)

			APP.catch.setTitle(dato.you_found)
			APP.catch.setText(dato.description)
			APP.catch.setAct(APP.labels.gameover_label)
			APP.catch.showTextOnly()
			APP.catch.show()

			// // APP.panel.setItemsNum( APP.game.getItems() )

			APP.bar.setBarIcon(2)

			APP.back.disable()

		},
		leave: function(option){ console.log('OverState :: leave')

			APP.catch.hide()
			APP.panel.hide()
			$('#fume').transition({opacity:0}, 500)
		}
	}

}
