;(function(window, $, undefined){

	APP.panel = {}

	$(document).ready(function(){

		var cont = $('#panel')
		var title = cont.find('[js_title]')
		var action = cont.find('[js_target_intro]')
		var btn = cont.find('#btnPlay')
		var itms = cont.find('[js_items]')
		var itms_best = cont.find('[js_items_best]')
		var ribb = cont.find('#ribbonSvg')
		var targetsSvg = cont.find('.target')
		var currentTarget

		targetsSvg.find('svg').attr('viewBox', '0 0 400 400')

		function show(delay){
			var d = (delay) ? delay : 0
			//cont.show()
			cont.transition({y:0, delay:d}, 1500, 'easeInOutQuint')
		}

		function hide(time, delay){
			var d = (delay) ? delay : 0
			//cont.hide()
			var h = cont.height() + 30
			var _t = (time>0) ? time : 0
			cont.transition({y:h, delay:d}, _t, 'easeInOutQuint')
		}

		function setTarget(id){

			TweenMax.set(targetsSvg, {scale:1})

			var targets = targetsSvg.find('svg > g')
			console.log(id)
			targets.hide()

			if(id>=0){
				currentTarget = $(targets.get(id))
				currentTarget.show()

				TweenMax.to(targetsSvg, .75, {scale:.9, repeat:-1, yoyo:true})
			}
		}

		function setTitle(v){
			title.html(v)
		}

		function setAction(v){
			console.log('setAction', v)
			action.text(v)
		}

		function setButton(url){
			btn.attr('href', url)
		}

		function setButtonLabel(v){
			btn.text(v)
		}


		function setItemsNum(v){
			itms.text(v)
		}

		


		function signalTarget(){
			TweenMax.killTweensOf(targetsSvg)
			TweenMax.set(targetsSvg, {scale:1.25})
			TweenMax.to(targetsSvg, 1.25, {scale:.5, ease:Elastic.easeInOut})
			//targetsSvg.css({scale:1.75, transformOrigin:'50% 50%'})
			//targetsSvg.transition({scale:1, transformOrigin:'50% 50%'}, 1500, 'easeOutQuint')
		}


		function setPartial(selector){
			cont.find('.find, .over, .won').hide()
			cont.find('.' + selector).show()

			cont.find('.svgover, .svgwon').css({opacity:0})
			cont.find('.svg' + selector).css({opacity:1})
		}


		function updateNumber(n, t){
			var nn = (n<10) ? '0'+n : n
			cont.find('[js_update_num]').text(nn + ' / ' + t)
		}


		function setGameOverTitle(str){
			cont.find('[gameover_panel_title]').text(str)
		}


		function dance(){
			cont.find('.svgwon #jumping > g').each(function(i, e){
				var gg = $(e).children()
				var homo = $(gg.get(1))
				if(Math.random()>.5) TweenMax.to(homo, .25+Math.random()*.25, {y:Math.random()*40*-1, repeat:-1, yoyo:true, ease:Expo.easeOut})
			})
		}

		function killDance(){
			cont.find('.svgwon #jumping > g').each(function(i, e){
				var gg = $(e).children()
				var homo = $(gg.get(1))
				TweenMax.killTweensOf(homo)
			})
		}


		APP.panel.dance = dance
		APP.panel.killDance = killDance
		APP.panel.setGameOverTitle = setGameOverTitle
		APP.panel.updateNumber = updateNumber
		APP.panel.setPartial = setPartial
		APP.panel.signalTarget = signalTarget
		APP.panel.setButtonLabel = setButtonLabel
		APP.panel.setItemsNum = setItemsNum
		APP.panel.setButton = setButton
		APP.panel.setAction = setAction
		APP.panel.setTitle = setTitle
		APP.panel.setTarget = setTarget
		APP.panel.show = show
		APP.panel.hide = hide

		$(document).ready(function(){
			APP.panel.show()
			APP.panel.setPartial('find')
			APP.panel.setTarget(2)

			
		})
	})



})(window, jQuery)