;(function(window, $, undefined){

	APP.home = {}

	$(document).ready(function(){

		var cont = $('#home')
		var logo = cont.find('.top')
		var p = cont.find('.middle .wrapper > *').css({opacity:0})

		var label = cont.find('[splash_label]')
		var title = cont.find('[splash_title]')

		function show(){
			
			cont.show()

			logo.css({opacity:1})
			
			cont.removeClass('bypass')

			cont.transition({opacity:1}, 500)

			p.each(function(i, e){
				$(this).transition({opacity:1, delay:i*200+500}, 1000)
			})
		}

		function hide(){
			p.each(function(i, e){
				$(this).transition({opacity:0}, 1000)
			})
			logo.css({opacity:0})
			cont.addClass('bypass')

			cont.transition({opacity:0, complete:function(){
				cont.hide()
			}}, 500)


		}

		function setLabel(v){
			label.text(v)
		}

		function setTitle(v){
			title.text(v)
		}

		APP.home.setLabel = setLabel
		APP.home.setTitle = setTitle
		APP.home.hide = hide
		APP.home.show = show
	})



})(window, jQuery)