function PlayState(){
    
	APP.bar.hide()

	return {
		enter: function(option){ console.log('PlayState :: enter');

			APP.game.start()


			APP.bar.show()
			APP.bar.setTarget( APP.game.getCurrent() )
			APP.bar.setNum( 0 )
			APP.bar.setNumTot( APP.game.getTotal() )
			APP.bar.signalTarget()

			APP.panel.setButton('#play')
			APP.panel.setPartial('find')

			APP.back.begin( APP.game.getCurrent() )

			APP.game.onTick(function(v, p){
	        	APP.bar.setTime(v, p)
		    })

		    APP.game.onOver(function(){
		    	APP.stateman.go('play.over')
		    })


		    APP.back.onClicked(function(){

		    	if(APP.game.over) return // enforcing concurrency

		    	if(APP.game.getLength() == 0){
		    		APP.stateman.go('play.win')
		    	}else{
		    		APP.stateman.go('play.catch')
		    	}
		    	
		    })


		},
		leave: function(option){ console.log('PlayState :: leave')

			APP.game.abort()

		}
	}

}
